/*=========================================================================================
  File Name: moduleTodoGetters.js
  Description: Todo Module Getters
==========================================================================================*/


const getters = {
  getLevelOptions: (state) => state.levelOptions,
  getLevel: state => status => {
    const _level = state.levelOptions.find(e => e.value === status) 
    return (_level)?_level:({ label: state.levelOptions[0].label, value: state.levelOptions[0].value  })
  },
  getLevelName: state => status => {
    const _level = state.levelOptions.find(e => e.value === status) 
    return (_level)?_level.label:""
  },

  getStatusOptions: (state) => state.statusOptions,
  getStatus: state => status => {
    const _status = state.statusOptions.find(e => e.value === status) 
    return (_status)?_status:({ label: state.statusOptions[0].label, value: state.statusOptions[0].value  })
  },
  getStatusName: state => status => {
    const _status = state.statusOptions.find(e => e.value === status) 
    return (_status)?_status.label:""
  },

  getRoleOptions: (state) => state.roleOptions,
  getRole: state => role => {
    const _role = state.roleOptions.find(e => e.value === role) 
    return (_role)?_role:({ label: state.roleOptions[0].label, value: state.roleOptions[0].value  })
  },
  getRoleName: state => role => {
    const _role = state.roleOptions.find(e => e.value === role) 
    return (_role)?_role.label:""
  },
/*
  roleOptions
  groupIdOptions
  positionOptions
  postTypeOptions
*/
  getGroupInfoOptions: (state) => state.groupInfoOptions,
  getGroupInfo: state => groupId => {
    const _groupInfo = state.groupInfoOptions.find(e => e.value === groupId) 
    return (_groupInfo)?_groupInfo:({ label: state.groupInfoOptions[0].label, value: state.groupInfoOptions[0].value  })
  },
  getGroupName: state => groupId => {
    const _groupInfo = state.groupInfoOptions.find(e => e.value === groupId) 
    return (_groupInfo)?_groupInfo.label:""
  },

  getPositionOptions: (state) => state.positionOptions,
  getPosition: state => position => {
    const _position = state.positionOptions.find(e => e.value === position) 
    return (_position)?_position:({ label: state.positionOptions[0].label, value: state.positionOptions[0].value  })
  },
  getPositionName: state => position => {
    const _position = state.positionOptions.find(e => e.value === position) 
    return (_position)?_position.label:""
  },
  getPostTypeOptions: (state) => state.postTypeOptions,
  getPostType: state => ( _value ) => {
    const postType = state.postTypeOptions.find(e => e.value === _value) 
    return (postType)?postType:({ label: state.postTypeOptions[0].label, 
      value: state.postTypeOptions[0].value,
      color: state.postTypeOptions[0].color,
      })
  },
  getIotDeviceType: state => ( _value ) => {
    return state.iotDeviceTypes.find(e => e.value === _value) 
  },
  getEtcDeviceType: state => ( _value ) => {
    return state.etcDeviceTypes.find(e => e.value === _value) 
  },

  getIotDataType: state => ( _value ) => {
    return state.iotDataTypes.find(e => e.value === _value) 
  },
  getFacilDataType: state => ( _value ) => {
    return state.facilDataTypes.find(e => e.value === _value) 
  },
}
export default getters