//import axios from '../../http/axios/index.js'
import axios from '@/axios.js'
//import acl from '@/acl/acl'

export default {
  //https://stackoverflow.com/questions/59836005/receiving-null-authorization-header-in-spring-boot-from-requests-with-angular-7
  SET_BEARER (state, payload) {
    // Set accessToken
    localStorage.setItem('accessToken', payload.accessToken)
    // Convert the JWT expiry time from seconds to milliseconds
    //  1    2 
    let tokenExpiry = new Date(payload.tmLogin+3600*1000*2) 
    localStorage.setItem('tokenExpiry', tokenExpiry)
    localStorage.setItem('loggedIn', 'true')

    axios.defaults.headers.common['Authorization'] = `Bearer ${payload.accessToken}`
    console.log(state)
    console.log(payload.accessToken)
    console.log("CHOI_DEBUG")
  },

  // Updates user info in state and localstorage
  UPDATE_USER_INFO(state, payload) {
    
    console.log("UPDATE_USER_INFO")
    // Get Data localStorage
    let userInfo = JSON.parse(localStorage.getItem("userInfo")) || state.currentUser
    console.log(userInfo)
    for (const property of Object.keys(payload)) {

      if (payload[property] != null) {
        // If some of user property is null - user default property defined in state.currentUser
        //state.currentUser[property] = payload[property]
        // Update key in localStorage
        userInfo[property] = payload[property]
      }
    }
    console.log(userInfo)
    // Store data in localStorage
    localStorage.setItem("userInfo", JSON.stringify(userInfo))
    Object.assign(state.currentUser, userInfo)
  },

  // SET_LOGOUT(state, logout) {
  //   acl.change('admin')
  //   //router.push('/pages/login').catch(() => {})
  // }

}

