  fetchUsers ({ commit }, payload) {
    const queryParam = { 
      pageNo: payload.pageNo,
      pageSize: payload.pageSize,
      lastOffset:(payload.pageNo - 1) * payload.pageSize, 
      sortField: (payload.sortField)?payload.sortField:'id',
      sortDir: (payload.sortDir)?payload.sortDir:'ASC',
      searchMap: (payload.searchMap)?payload.searchMap:{},
      searchByOrMap: (payload.searchByOrMap)?payload.searchByOrMap:{}
    }
    return new Promise((resolve, reject) => {
      axios.post('/prototype/user/page',queryParam)
        .then((response) => {
          commit('SET_USERS', { 
            'totalCount':response.data.totalCnt,
            'pageSize':response.data.pageSize,
            'pageNo': response.data.pageNo,
            'users': response.data.item })
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  fetchUser (context, item) {
    return new Promise((resolve, reject) => {
      axios.get(`/prototype//user/${item.userId}`)
        .then((response) => {
          if ( response.data.resultCode !== '0001' ) {              
            throw "An error raised in retrieving user info"
          }
          resolve(response)
        })
        .catch((error) => { 
          item.owner.openConfirmFailure("","  .")
          reject(error) 
        })
    })
  },
  checkLoginId (context, item) {
    return new Promise((resolve, reject) => {
      axios.post(`/prototype//user/check/loginid/${item.loginId}`)
        .then((response) => {
          if ( response.data.resultCode === ReturnCodes.ALREADY_EXISTS_USERID ) {
            //item.owner.openConfirmFailure("",`Login id[${item.loginId}]  .`)
            resolve({ alreadyExists:true})
          } else if ( response.data.resultCode !== ReturnCodes.SUCCESS ) {              
            throw "An error raised in checking login id"
          }
          resolve({ alreadyExists:false})
        })
        .catch((error) => { 
          //item.owner.openConfirmFailure("","  ")
          reject(error) 
        })
    })
  },
  checkUserEmail (context, item) {
    return new Promise((resolve, reject) => {
      axios.post("/prototype//user/check/useremail", { simpleStr: item.userEmail })
        .then((response) => {
          if ( response.data.resultCode === ReturnCodes.ALREADY_EXISTS_EMAIL ) {
            //item.owner.openConfirmFailure("",`[${item.userEmail}]  .`)
            resolve({ alreadyExists:true})
          } else if ( response.data.resultCode !== ReturnCodes.SUCCESS ) {              
            throw "An error raised in checking User email"
          }
          resolve({ alreadyExists:false})
        })
        .catch((error) => { 
          //item.owner.openConfirmFailure("","  .")
          reject(error) 
        })
    })
  },
  deleteUser ({ commit }, userId) {
    return new Promise((resolve, reject) => {
      axios.delete(`/prototype//user/${userId}`)
        .then((response) => {
          commit('REMOVE_USER', userId)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  addUser(context, item) {
    return new Promise((resolve, reject) => {
      axios.post("/prototype//user/add", item.userInfo)
        .then((res) => {
          //commit('ADD_USER', response.data.item)
          if ( res.data.resultCode !== '0001' ) {
            throw " "
          } else if ( res.data.resultCode !== '0001' ) {              
            throw " "
          }
          item.owner.openConfirmSuccess(""," .")

          resolve(res)
        })
        .catch((error) => { 
          item.owner.openConfirmFailure("","  .")
          reject(error) 
        })
    })
  },
  updateUser({ commit, rootState }, item) {
    return new Promise((resolve, reject) => {
      axios.post("/prototype//user/update", item.userInfo)
        .then((res) => {
          if ( res.data.resultCode !== '0001' ) {
            throw "An error raised in saving user info"
          } else if ( res.data.resultCode !== '0001' ) {              
            throw "An error raised in saving user info"
          }
          let userInfo = res.data.item
          if (rootState.auth.currentUser.id === userInfo.id) {
            // Update user details
            commit('auth/UPDATE_USER_INFO', userInfo, { root: true })
          }
          item.owner.openConfirmSuccess(""," .")
          resolve(res)
        })
        .catch((error) => { 
          item.owner.openConfirmFailure("","  .")
          reject(error) 
        })
    })
  },
  //commit('UPDATE_USER_INFO', newUserData)
