import { mapState, mapGetters, mapActions } from 'vuex'

export const authComputed = {
  ...mapState('auth', {
    currentUser: (state) => state.currentUser,
  }),
  ...mapGetters('auth', ['loggedIn','isUserLoggedIn','hasAdminRole']),
}

export const layoutComputed = {
  ...mapState('layout', {
    layoutType: (state) => state.layoutType,
    leftSidebarType: (state) => state.leftSidebarType,
    layoutWidth: (state) => state.layoutWidth,
    topbar: (state) => state.topbar,
    loader: (state) => state.loader
  })
}

export const commonConfigComputed = {
  ...mapState('commonConfig', {
    levelOptions: (state) => state.levelOptions,
    statusOptions: (state) => state.statusOptions,//USER_STATUS
    roleOptions: (state) => state.roleOptions,
    groupInfoOptions: (state) => state.groupInfoOptions,
    iotDeviceTypes: (state) => state.iotDeviceTypes,
    etcDeviceTypes: (state) => state.etcDeviceTypes,
    iotDataTypes: (state) => state.iotDataTypes,
    facilDataTypes: (state) => state.facilDataTypes,
  }),
  ...mapGetters('commonConfig', ['getLevel','getStatus','getRole','getGroupInfo','getPosition','getPostType',
  'getLevelName','getStatusName','getRoleName','getGroupName','getPositionName','getIotDeviceType','getEtcDeviceType',
  'getIotDataType','getFacilDataType',
  ]),
}

export const noticeBoardComputed = {
  ...mapState('noticeBoard', {
    myNotices: (state) => state.myNotices,
  })
}

export const authMethods = mapActions('auth', ['logIn', 'logOut', 'register', 'resetPassword'])

export const layoutMethods = mapActions('layout', ['changeLayoutType', 'changeLayoutWidth', 'changeLeftSidebarType', 'changeTopbar', 'changeLoaderValue'])

export const authFackMethods = mapActions('authfack', ['login', 'registeruser', 'logout'])

export const notificationMethods = mapActions('notification', ['success', 'error', 'clear'])
