/*=========================================================================================
  File Name: moduleTodoMutations.js
  Description: Todo Module Mutations
==========================================================================================*/


export default {
  SET_COMMON_CODES (state, item) {
    state.statusOptions = item.filter(e => e.groupId === "USER_STATUS").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "color":e.description }})
    state.roleOptions   = item.filter(e => e.groupId === "USER_ROLE").map( e=> { return { "label":e.itemValue, "value":e.itemKey }})
    state.groupInfoOptions = item.filter(e => e.groupId === "GROUP_INFO").map( e=> { return { "label":e.itemValue, "value":e.itemKey }})
    state.langOptions = item.filter(e => e.groupId === "LANG").map( e=> { return { "label":e.itemValue, "value":e.itemKey }})
    state.countryOptions = item.filter(e => e.groupId === "COUNTRY").map( e=> { return { "label":e.itemValue, "value":e.itemKey }})
    state.snsOptions = item.filter(e => e.groupId === "SNS").map( e=> { return { "type":e.itemKey, "url":e.itemValue }})
    state.consultTypeChoices = item.filter(e => e.groupId === "CONSULT_TYPE").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "color":e.description }})
    state.consultStatusChoices = item.filter(e => e.groupId === "CONSULT_STATUS").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "color":e.description }})
    state.postTypeOptions = item.filter(e => e.groupId === "POST_TYPE").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "color":e.description }})

    state.iotDeviceTypes = item.filter(e => e.groupId === "IOT_DEVICE_TYPE").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "description":e.description }})
    state.etcDeviceTypes = item.filter(e => e.groupId === "ETC_DEVICE_TYPE").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "description":e.description }})

    state.iotDataTypes = item.filter(e => e.groupId === "IOT_DATA_KEY").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "description":e.description }})
    state.facilDataTypes = item.filter(e => e.groupId === "TAG_DATA_TYPE").map( e=> { return { "label":e.itemValue, "value":e.itemKey, "description":e.description }})

  },
  SET_BIZ_INFOS (state, item) {
    state.bizInfoOptions = item.map( e=> { return { "label":e.name, "value":e.id }})
  },
}