/*=========================================================================================
  File Name: moduleCalendarMutations.js
  Description: Calendar Module Mutations
==========================================================================================*/


export default {
  TEST_NOTICES (state) {
    state.totalPage+=1
    state.currentPage+=1
  },
  SET_NOTICES (state, payload) {
    state.pageSize=payload.pageSize
    state.totalCount=payload.totalCount
    state.totalPage=Math.ceil(payload.totalCount/payload.pageSize)
    state.currentPage=payload.pageNo
    state.notices=payload.notices
  },
  ADD_NOTICE (state, notice) {
    state.notices.push(notice)
  },
  // SET_NOTICE (state, notice) {
  //   state.notices = notice
  // },
  REMOVE_NOTICE (state, id) {
    const noticeIndex = state.notices.findIndex((u) => u.id === id)
    state.notices.splice(noticeIndex, 1)
  },
  UPDATE_NOTICE (state, notice) {
    const noticeIndex = state.notices.findIndex((u) => u.id === notice.id)
    Object.assign(state.notices[noticeIndex], notice)
  },
  SET_MY_NOTICES (state, notices) {
    state.myNotices = notices
  },
  UPDATE_MY_NOTICE (state, notice) {
    const noticeIndex = state.myNotices.findIndex((u) => u.id === notice.id)
    Object.assign(state.myNotices[noticeIndex], notice)
  },
  ADD_MY_NOTICE (state, notice) {
    state.myNotices.push(notice)
  },
  REMOVE_MY_NOTICE (state, id) {
    const noticeIndex = state.myNotices.findIndex((u) => u.id === id)
    state.myNotices.splice(noticeIndex, 1)
  },
  INC_READ_CNT (state, id) {
    const noticeIndex = state.notices.findIndex((u) => u.id === id)
    state.notices[noticeIndex].readCnt+=1
  },
  SET_QUERY_PARAM (state, queryParam) {
    state.queryParam = queryParam
  },

  SET_COMMENTS (state, payload) {
    state.pageSizeComment=payload.pageSize
    state.totalCountComment=payload.totalCount
    state.totalPageComment=Math.ceil(payload.totalCount/payload.pageSize)
    state.currentPageComment=payload.pageNo
    state.comments=payload.comments
  },
  ADD_COMMENT (state, comment) {
    state.comments.push(comment)
  },
  // SET_NOTICE (state, notice) {
  //   state.notices = notice
  // },
  REMOVE_COMMENT (state, id) {
    const commentIndex = state.comments.findIndex((u) => u.id === id)
    state.comments.splice(commentIndex, 1)
  },
  UPDATE_COMMENT (state, comment) {
    const commentIndex = state.comments.findIndex((u) => u.id === comment.id)
    Object.assign(state.comments[commentIndex], comment)
  },
  SET_QUERY_PARAM_COMMENT (state, queryParam) {
    state.queryParamComment = queryParam
  },

}
