import Swal from "sweetalert2";

export default {
  install (Vue) {
    Vue.mixin({
      methods: {
        openConfirm(title="Confirm",
            msg, 
            acceptfunc
            //, objectParam
        ) {
          let func = acceptfunc
          Swal.fire({
            title: title,
            text: msg,
            showCancelButton: true,
            confirmButtonText:"",
            cancelButtonText:""
          }).then((result) => {
            if (result.isConfirmed) {
              func()
            }
          })
        },
        openConfirmSuccess(title, msg) {
          Swal.fire(title,msg);
        },
        openConfirmFailure(title, msg) {
          Swal.fire(title,msg);
        },
        // notifySuccess(title, msg) {
        //   this.$vs.notify({ color: 'success',title: title, text: msg })
        // },
        // notifyFailure(title, msg) {
        //   this.$vs.notify({ color: 'danger',title: title, text: msg })
        // },
        // acceptCommentDelete(str) {
        //   this.$vs.notify({ color: 'primary',title: str+'Deleted comment', text: 'The selected comment was successfully deleted' })
        // },

        // {
        //   title: '<strong>HTML <u>example</u></strong>',
        //   icon: 'info',
        //   html:
        //     'You can use <b>bold text</b>, ' +
        //     '<a href="//sweetalert2.github.io">links</a> ' +
        //     'and other HTML tags',
        //   showCloseButton: true,
        //   showCancelButton: true,
        //   focusConfirm: false,
        //   confirmButtonText:
        //     '<i class="fa fa-thumbs-up"></i> Great!',
        //   confirmButtonAriaLabel: 'Thumbs up, great!',
        //   cancelButtonText:
        //     '<i class="fa fa-thumbs-down"></i>',
        //   cancelButtonAriaLabel: 'Thumbs down'
        // }
      },
    })
  }
}