//import jwt from '@/services/auth/jwt.js'
import axios from '@/axios.js'

import router from '@/router'
//import acl from '@/helpers/acl/acl'

export default {
  // JWT
  loginJWT ({ commit }, payload) {

    return new Promise((resolve, reject) => {
      axios.post('/auth/login', { 
        loginId: payload.userDetails.loginId,
        passwd: payload.userDetails.password
      }).then(response => {

          // If there's user data in response
          if (response.data.resultCode === "0001" && response.data.item.userInfo ) {
            let userInfo = response.data.item.userInfo
            let accessToken  = response.data.item.token
            console.log(response.data.item.userInfo)
            console.log(response.data.item.token)
            //console.log(router.currentRoute)
            //const URL = process.env.NODE_ENV === 'production'?process.env.VUE_APP_PROD_URL: process.env.VUE_APP_DEV_URL
            let tmLogin= Date.now()
            userInfo.exp = tmLogin
            // Update user details
            commit('UPDATE_USER_INFO', userInfo)//, {root: true}
            console.log("aa")
            // Set bearer token in axios
            commit('SET_BEARER', { accessToken: accessToken, tmLogin: tmLogin })
            console.log("bvb")
            // Navigate User to homepage
            router.push(router.currentRoute.query.to || '/').catch((err) => { console.log(err)})
            resolve(response)
          } else {
            reject({message: 'Login Id  . '})
          }

        })
        .catch(error => { reject(error) })
    })
  },

  logout() {
    return new Promise(() => {
      console.log("logout promise")
      //acl.change('admin')
    })
  },
  
  //
  fetchAccessToken ({ commit }) {
    return new Promise((resolve, reject) => {
      axios.post('/auth/refreshToken',{}).then(response => { 
        // If there's user data in response
        if (response.data.resultCode === "0001" && response.data.item ) {
          let accessToken  = response.data.item
          let tmLogin= Date.now()
          // Set bearer token in axios
          commit('SET_BEARER', { accessToken: accessToken, tmLogin: tmLogin })
          // Navigate User to homepage
          //router.push(router.currentRoute.query.to || '/').catch((err) => { console.log(err)})
          resolve(accessToken)
        } else {
          reject({message: '  .   . '})
        }

      })
      .catch(error => { reject(error) })
    })
  }
}
