import axios from '@/axios.js'

export default {
  fetchAllCommonCodes ({ commit }) {
    return new Promise((resolve, reject) => {
      axios.post("/common/list")
      .then((response) => {
        commit('SET_COMMON_CODES', response.data.item)
        resolve(response)
      })
      .catch((error) => { reject(error) })
    })
  },
  fetchCommonCodes ({ commit }, payload) {
    const queryParam = { 
      searchMap: (payload.searchMap)?payload.searchMap:{},
      searchByOrMap: (payload.searchByOrMap)?payload.searchByOrMap:{}
    }
    return new Promise((resolve, reject) => {
      axios.post("/common/search",queryParam)
        .then((response) => {
          commit('SET_COMMON_CODES', response.data.item)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
}
