/*=========================================================================================
  File Name: moduleCalendarGetters.js
  Description: Calendar Module Getters
==========================================================================================*/


export default {
  totalPages: (state) => state.totalPage | 0,
  getNotice: (state) => noticeId => state.notices.find((notice) => notice.id === noticeId),
  totalPagesComment: (state) => state.totalPageComment | 0,
  getComment: (state) => commentId => state.comments.find((comment) => comment.id === commentId),
}
