import SockJS from 'sockjs-client';
import Stomp from 'webstomp-client';
import store from '@/state/store.js'

class StompService {
    queueId = "A01" //
    token = ""
    socket = null
    stompClient = null
    
    //
    setqueue(_quequeId) {
            this.queueId = _quequeId
    }
    send(_msg) {
        console.log("Send message:" + _msg);
        if (this.stompClient && this.stompClient.connected) {
            const msg = { name: _msg };
            console.log(JSON.stringify(msg));
            this.stompClient.send("/app/sendmsg", JSON.stringify(msg), {});
        }
    }

    //
    start(queueId) {
        this.queueId = queueId
        console.log("stomp start :" + this.queueId)
    }
    //
    end() {
        console.log("stomp end :" + this.queueId)
    }
    //
    call() {
        console.log("stomp call :" + this.queueId)
    }

    connect() {
        console.log("stomp connect")
        
        //this.setqueue("A01")
        const token = localStorage.getItem('accessToken')
        if (!token) {
            console.log("stomp connect fail no token")
            return
        }
        if (this.connected) {
            console.log("stomp already connected. stompClient:"+this.stompClient)
            return
        }
        const socUrl = ((process.env.NODE_ENV === 'production')?process.env.VUE_APP_PROD_URL: process.env.VUE_APP_DEV_URL)+"/any-socket?token="+token
        console.log("calling socUrl:"+socUrl)
        this.socket = new SockJS(socUrl);
        this.stompClient = Stomp.over(this.socket);
        this.stompClient.connect({},
            frame => {
                this.connected = true;
                console.log(frame);
/*
{"type":"MessageQueue.Insert",
  "sendDtm":"20200606225857148",
  "payload":"{"id":26,"customerId":"7253632591","chatId":6,"direction":"I","bizId":"A01","country":"kr","lang":"ko","status":null,"message":"It's worth noting that the keyHolder object will contain the auto-generated key return from th","createdDtm":"2020-06-07 07:58:57.130"}"}
{"type":"ChatSessionStatus.Update",
"sendDtm":"20200606225857145",
"payload":"{"id":1,"chatId":6,"customerId":"7253632591","agentId":"P060113","lastMessageId":26,"lastMessage":"It's worth noting that the keyHolder object will contain the auto-generated key return from th","unreadCnt":1,"direction":"I","bizId":"A01","country":"kr","lang":"ko","updatedDtm":"2020-05-31 21:39:43.677000000","createdDtm":"2020-05-04 10:21:20.838000000"}"}
 */
                //
                const receiverId = 'all'
                this.stompClient.subscribe(`/queue/${receiverId}/chat`, tick => {
                    console.log(tick.body);
                    var tickBoady = JSON.parse(tick.body)
                    var msgType = tickBoady.type
                    console.log(`msgType: ${msgType}`);
                    var msgPayload =JSON.parse(tickBoady.payload)
                    console.log(`msgPayload: ${msgPayload}`);
                    if (msgType==='noticeBoard.add') { // chat session add
                        const notice =JSON.parse(tickBoady.payload)
                        store.commit('noticeBoard/ADD_MY_NOTICE', notice)
                    } else if (msgType==='noticeBoard.delete') { // chat session remove
                        const noticeId =JSON.parse(tickBoady.payload)
                        store.commit('noticeBoard/REMOVE_MY_NOTICE', noticeId)
                    } else if (msgType==="plantInfo.add") {
                        const plant =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_PLANT_INFO', plant)
                    } else if (msgType==="plantInfo.delete") {
                        const plantId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_PLANT_INFO', plantId)
                    } else if (msgType==="plantInfo.update") {
                        const plant =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_PLANT_INFO', plant)
                    } else if (msgType==="areaInfo.add") {
                        const area =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_AREA_INFO', area)
                    } else if (msgType==="areaInfo.delete") {
                        const areaId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_AREA_INFO', areaId)
                    } else if (msgType==="areaInfo.update") {
                        const area =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_AREA_INFO', area)
                    } else if (msgType==="facilityInfo.add") {
                        const facility =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_FACILITY_INFO', facility)
                    } else if (msgType==="facilityInfo.delete") {
                        const facilityId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_FACILITY_INFO', facilityId)
                    } else if (msgType==="facilityInfo.update") {
                        const facility =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_FACILITY_INFO', facility)
                    } else if (msgType==="interiorInfo.add") {
                        const interior =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_INTERIOR_INFO', interior)
                    } else if (msgType==="interiorInfo.delete") {
                        const interiorId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_INTERIOR_INFO', interiorId)
                    } else if (msgType==="interiorInfo.update") {
                        const interior =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_INTERIOR_INFO', interior)
                    } else if (msgType==="iotDeviceInfo.add") {
                        const iotDevice =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_IOT_DEVICE_INFO', iotDevice)
                    } else if (msgType==="iotDeviceInfo.delete") {
                        const iotDeviceId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_IOT_DEVICE_INFO', iotDeviceId)
                    } else if (msgType==="iotDeviceInfo.update") {
                        const iotDevice =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_IOT_DEVICE_INFO', iotDevice)
                    } else if (msgType==="etcDeviceInfo.add") {
                        const etcDevice =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_ETC_DEVICE_INFO', etcDevice)
                    } else if (msgType==="etcDeviceInfo.delete") {
                        const etcDeviceId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_ETC_DEVICE_INFO', etcDeviceId)
                    } else if (msgType==="etcDeviceInfo.update") {
                        const etcDevice =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_ETC_DEVICE_INFO', etcDevice)
                    } else if (msgType==="iotRuleInfo.add") {
                        const iotRule =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_IOT_RULE_INFO', iotRule)
                    } else if (msgType==="iotRuleInfo.delete") {
                        const iotRuleId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_IOT_RULE_INFO', iotRuleId)
                    } else if (msgType==="iotRuleInfo.update") {
                        const iotRule =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_IOT_RULE_INFO', iotRule)

                    } else if (msgType==="facilRuleInfo.add") {
                        const facilRule =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/ADD_FACIL_RULE_INFO', facilRule)
                    } else if (msgType==="facilRuleInfo.delete") {
                        const facilRuleId =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/REMOVE_FACIL_RULE_INFO', facilRuleId)
                    } else if (msgType==="facilRuleInfo.update") {
                        const facilRule =JSON.parse(tickBoady.payload)
                        store.commit('plantInfo/UPDATE_FACIL_RULE_INFO', facilRule)
                    }

                })
            },
            error => {
                console.log(error);
                this.connected = false;
            }
        )
    }
    disconnect() {
        console.log("stomp disconnect")
        if (this.stompClient) {
            this.stompClient.disconnect();
        }
        this.connected = false;
    }
    tickleConnection() {
        this.connected ? this.disconnect() : this.connect();
    }
}

export default new StompService()