// axios
import axios from 'axios'
import authService from '@/services/auth/authService'
import store  from '@/state/store'

//const baseURL = ""
let isAlreadyFetchingAccessToken = false
let subscribers = []
const URL = process.env.NODE_ENV === 'production'?process.env.VUE_APP_PROD_URL: process.env.VUE_APP_DEV_URL
const vueURL = process.env.NODE_ENV === 'production'?process.env.VUE_APP_PROD_VUE_URL: process.env.VUE_APP_DEV_VUE_URL
//CHOI_DEBUG
function onAccessTokenFetched (access_token) {
  console.log("onAccessTokenFetched")
  subscribers = subscribers.filter(callback => callback(access_token))
}

function addSubscriber (callback) {
  console.log("addSubscriber")
  subscribers.push(callback)
}

console.log("=================================================")
console.log("Create Axios")
console.log("=================================================")
const axiosA = axios.create({
  baseURL: URL,
  //baseURL: "http://file.rozetatech.com:8888",
  withCredentials: true,
   headers: {
    //"Access-Control-Allow-Origin": vueURL,
    "Access-Control-Allow-Origin": '*',
   }
})

console.log(authService)

if (authService.isAuthenticated ()) {
// CHOI_DEBUG
// if (store.getters['auth/isUserLoggedIn']) {
  axiosA.defaults.headers.common['Authorization'] = `Bearer ${localStorage.getItem('accessToken')}`
}

axiosA.interceptors.request.use(function (request) {
  console.log("URL====>"+URL+request.url)
  console.log("vueURL====>"+vueURL)
  console.log("axiosA.interceptors.request")
  console.log(request)
  return request;
}, function (error) {
  // Any status codes that falls outside the range of 2xx cause this function to trigger
  // Do something with response error
  return Promise.reject(error);
});
let CHOIDEBUG=true
const VUESJS_VERSION="vuejs-version"
axiosA.interceptors.response.use(function (res) {
  console.log("axiosA.interceptors.response")

  //https://blog.francium.tech/vue-js-cache-not-getting-cleared-in-production-on-deploy-656fcc5a85fe
  let vuejsVersion=res.headers[VUESJS_VERSION]||'default'
  //console.log(`vuejs.version `+response.headers['vuejs.version']+":response.config.method "+response.config.method)
  //console.log(res.headers)
  
  if (vuejsVersion !== localStorage.getItem(VUESJS_VERSION)  && res.config.method == 'get')  {
    localStorage.setItem(VUESJS_VERSION, vuejsVersion)
    store.dispatch('notification/success', '  .    .');
    //window.location.reload()//
  }
  return res;
}, function (error) {
  // const { config, response: { status } } = error
  const { config, response } = error
  const originalRequest = config
  console.log("response error")
  console.log(response)
  // if (status === 401) {
  if (response && response.status === 401) {
    console.log("401===>  isAlreadyFetchingAccessToken="+isAlreadyFetchingAccessToken)

    if (CHOIDEBUG) {
    //if (!isAlreadyFetchingAccessToken) {
      console.log("401====>2")
      isAlreadyFetchingAccessToken = true
      //CHOI_DEBUG
      store.dispatch('auth/fetchAccessToken')
        .then((access_token) => {
          isAlreadyFetchingAccessToken = false
          onAccessTokenFetched(access_token)
        })
    } else {
      console.log(error)
      // if user is logged in via auth0
      authService.logOut()
    }

    const retryOriginalRequest = new Promise((resolve) => {
      addSubscriber(access_token => {
        originalRequest.headers.Authorization = `Bearer ${access_token}`
        resolve(axios(originalRequest))
      })
    }, (error)=> {
      console.log(error)
    })
    return retryOriginalRequest
  }
  return Promise.reject(error)
});

export default axiosA
