import Vue from 'vue'
import Vuex from 'vuex'

import layout from './modules/layout'
import notification from './modules/notification'
import commonConfigModule from './modules/common-config/moduleCommonConfig'
import authModule from './modules/auth/moduleAuth'
import noticeBoardModule from './modules/notice-board/moduleNoticeBoard'
import userManagementModule from './modules/user-management/moduleUserManagement'
// import states from './states.js'
// import getters from './getters.js'
// import mutations from './mutations.js'

Vue.use(Vuex)

const store = new Vuex.Store({
  // state: states,
  // getters: getters,
  // mutations: mutations,
  modules: { 
    layout: layout,
    notification: notification,
    commonConfig: commonConfigModule,
    auth: authModule,
    noticeBoard: noticeBoardModule,
    userMgmt: userManagementModule,
  },
// Enable strict mode in development to get a warning
  // when mutating state outside of a mutation.
  // https://vuex.vuejs.org/guide/strict.html

  strict: process.env.NODE_ENV !== 'production',
})

export default store

