  fetchMyNotices ({ commit }, item) {
    return new Promise((resolve, reject) => {
      axios.post(`/prototype//notice-board/mynotices`, { userId: item.userId })
      .then((response) => {
        if (response.data.resultCode !== "0001") throw "No data"
        commit('SET_MY_NOTICES', response.data.item )
        resolve(response)
      })
      .catch((error) => { 
        item.owner.openConfirmFailure("",` `)
        reject(error) 
      })
    })
  },
