import auth from '@/services/auth/authService'

export default {
  isUserLoggedIn: (state) => {
    console.log('isUserLoggedIn')
    return state.currentUser&& auth.isAuthenticated() 
  },
  hasAdminRole: (state) => {
    return ( state.currentUser.level==='A' )
  }
}
